import styled from 'styled-components';

export default (component) => styled(component)`
  margin: 20px auto;
  .explorer__item {
    display: inline-block;
    width: 20%;
    margin-right: 15px;
    height: 72px;
  }
  .item__photo {
    width: 30%;
    height: 100%;
    object-fit: cover;
  }
  .item__title {
    display: inline-block;
    height: 100%;
    margin-left: 12%;
    vertical-align: middle;
    font-weight: 800;
    line-height: 1.28571em;
    color: rgb(72, 72, 72)
  }
`;
